/* -------------------------------------------------------------------------- */
/* Layouts: Portofolio                                                          */
/* -------------------------------------------------------------------------- */

.portofolio {
  $f: &;
  padding-top: 64px;
  padding-bottom: 40px;
  @include large {
    padding-top: 48px;
    padding-bottom: 24px;
  }
  @include medium {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  @include small {
    padding-top: 24px;
    padding-bottom: 24px;
  }
  &__head {
    text-align: center;
    margin-bottom: 48px;
    @include medium {
      margin-bottom: 40px;
    }
    @include small {
      margin-bottom: 32px;
    }
  }
  &__desc {
    margin-bottom: 0;
    max-width: 584px;
    margin-left: auto;
    margin-right: auto;
    @include font-size(h5);
    font-weight: fw(medium);
    @include small {
      @include font-size(label);
    }
  }
  &__list {
    @include clearfix;
    margin-left: -12px;
    margin-right: -12px;
    @include medium {
      margin-left: -16px;
      margin-right: -16px;
      @include scroll("x", "touch", "hide");
    }
    @include small {
      margin-left: -24px;
      margin-right: -24px;
    }
  }
  #{$f} {
    &__item {
      float: left;
      width: 33.33333%;
      padding: 12px;
      @include medium {
        width: 50%;
        padding: 16px;
      }
      @include small {
        width: 100%;
        padding: 12px 24px;
      }
    }
  }
}
